/* src/styles/Header.css */

.minimal-header {
  background-color: transparent !important;
  backdrop-filter: none !important;
  box-shadow: none !important;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}

.minimal-header.scrolled {
  background-color: rgba(0, 0, 0, 0.5); /* This can be adjusted for scrolled background if needed */
  backdrop-filter: none !important;
  box-shadow: none !important;
}

.nav-container {
  display: flex;
  justify-content: flex-end;
  padding: 10px 20px; /* Adjust padding as needed */
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-item {
  margin-left: 20px;
}

.nav-link {
  color: white; /* Ensure good contrast with background image */
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #ccc;
}