/* src/styles/Footer.css */

@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

#footer {
    background: #adadad;
    color: white;
    padding: 20px 0;
    width: 100%;
    position: relative;
}

#footer .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.quick-links h5 {
    margin-bottom: 10px;
    color: #ffffff;
}

.quick-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.quick-links ul li {
    margin-bottom: 10px;
}

.quick-links ul li a {
    color: #ffffff;
    text-decoration: none;
}

.quick-links ul li a i {
    margin-right: 5px;
}

.social-links {
    display: flex;
    align-items: center;
}

.social-links .list-inline {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
}

.social-links .list-inline-item {
    margin: 0 10px;
}

.social-links .list-inline-item a {
    color: white;
    font-size: 24px;
    transition: color 0.3s ease;
}

.social-links .list-inline-item a:hover {
    color: #eeeeee;
}

.footer-bottom {
    text-align: center;
    font-size: 14px;
    border-top: 1px solid #eeeeee;
    padding-top: 10px;
    margin-top: 20px;
    color: #ffffff;
}

.footer-bottom a {
    color: #ffffff;
    text-decoration: none;
}

.footer-bottom a:hover {
    color: #eeeeee;
}

/* Responsive Styles */
@media (max-width: 767px) {
    .footer-content {
        flex-direction: column;
        align-items: flex-start;
    }

    .social-links {
        margin-top: 10px;
        justify-content: center;
        width: 100%;
    }

    .quick-links h5,
    .quick-links ul li a {
        text-align: left;
    }
}