.about-page {
    padding: 0 20px;
  }
  
  .hero-about {
    position: relative;
    height: 60vh;
    background: url('../images/ELITE.jpg') no-repeat center center/cover;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
  }
  
  .hero-content {
    position: relative;
    color: #fff;
    text-align: center;
  }
  
  .about-story {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 40px 0;
  }
  
  .story-content {
    flex: 1 1 50%;
    padding: 20px;
  }
  
  .story-image-skills-container {
    display: flex;
    flex: 1 1 50%;
    align-items: flex-start;
  }
  
  .story-image {
    flex: 1;
    padding: 20px;
  }
  
  .story-image img {
    width: 100%;
    border-radius: 8px;
  }
  
  .skills-section {
    flex: 1;
    padding: 20px;
  }
  
  .skills-section h2 {
    margin-bottom: 20px;
  }
  
  .skills-section ul {
    list-style: none;
    padding: 0;
  }
  
  .skills-section ul li {
    background: #f0f0f0;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
  }
  
  .about-values {
    background-color: #f8f8f8;
    padding: 40px 20px;
  }
  
  .values-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .value-item {
    flex: 1;
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .cta {
    text-align: center;
    padding: 40px 20px;
    background: #caa8a8;
  }
  
  .cta-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    border-radius: 5px;
    text-decoration: none;
    transition: background 0.3s;
  }
  
  .cta-button:hover {
    background-color: #555;
  }
  
  @media (max-width: 768px) {
    .story-image-skills-container {
      flex-direction: column;
    }
  }
  