/* src/styles/Boudoir.css */

@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800&display=swap');

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.carousel .responsive-container-block {
  min-height: 75px;
  height: fit-content;
  width: 100%;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: flex-start;
}

.carousel * {
  font-family: Nunito, sans-serif;
}

.carousel .responsive-container-block.Container {
  max-width: 1320px;
  margin: 0 auto;
  position: relative;
  padding: 10px;
}

.carousel .cardImg {
  width: 100%;
}

.carousel .swiper.mySwiper {
  width: 100%;
}

.carousel .swiper-slide {
  width: 100%;
}

.carousel .swiper-wrapper {
  width: 100%;
}

.carousel .swiper-pagination.container-block {
  top: auto;
  bottom: 5px;
  min-height: 20px;

  /* src/styles/Boudoir.css */
.swiper-button-next,
.swiper-button-prev {
  color: #fff; /* Set to your desired color */
  /* You can add additional styles here */
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
  color: #ccc; /* Hover color */
}

/* src/styles/Boudoir.css */

.carousel {
    position: relative;
  }
  
  .carousel .hover-nav {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 50px;
    z-index: 10;
    cursor: pointer;
  }
  
  .carousel .hover-nav.left {
    left: 0;
  }
  
  .carousel .hover-nav.right {
    right: 0;
  }
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .carousel .responsive-container-block.Container {
    padding: 10px;
  }
}

@media (max-width: 768px) {
  .carousel .responsive-container-block.Container {
    padding: 10px;
  }
}

@media (max-width: 500px) {
  .carousel .responsive-container-block.Container {
    padding: 10px;
  }
}