/* src/styles/ContactPage.css */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap');

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

.section_10 {
  width: 100%;
  padding: 40px 20px;
  background-color: #5a5959; /* Soft gray background */
}

.section_10 * {
  font-family: 'Poppins', sans-serif;
}

.section_10 .responsive-container-block.container {
  max-width: 900px;
  margin: 20px auto 80px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.1);
}

.section_10 .responsive-container-block.form-container {
  width: 100%;
  padding: 40px 20px;
  border-radius: 15px;
  background: #ffffff; /* Pure white background */
  border: 1px solid #e5e5e5;
}

.section_10 .input, 
.section_10 .textinput {
  width: 100%;
  background-color: #fafafa; /* Light background for inputs */
  border: 1px solid #ccc; /* Soft border color */
  padding: 15px;
  font-size: 16px;
  margin-bottom: 20px;
  border-radius: 8px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.section_10 .input:focus,
.section_10 .textinput:focus {
  border-color: #af4261; /* Accent color for focus */
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
  outline: none;
}

.section_10 .text-blk.input-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
  color: #333; /* Dark text color for better readability */
}

.section_10 .submit-btn {
  width: 100%;
  height: 50px;
  background-color: #af4261; /* Accent color */
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.section_10 .submit-btn:hover {
  background-color: #8e2b52; /* Darker shade for hover effect */
  transform: translateY(-2px);
}

.section_10 .submit-btn:active {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .section_10 .responsive-container-block.container {
    max-width: 100%;
    padding: 10px;
  }

  .section_10 .responsive-container-block.form-container {
    padding: 20px;
  }
}

@media (max-width: 500px) {
  .section_10 .responsive-container-block.container {
    max-width: 100%;
    padding: 0 10px;
  }

  .section_10 .responsive-container-block.form-container {
    padding: 15px;
  }
}