/* src/styles/HomePage.css */

body, html {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    color: #333;
    background-color: #f5f5f5;
    scroll-behavior: smooth;
  }
  
  .homepage {
    position: relative;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    position: absolute;
    width: 100%;
    z-index: 1000;
    box-sizing: border-box;
  }
  
  .navbar .logo {
    font-size: 24px;
    font-weight: bold;
    color: white;
  }
  
  .navbar nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
    gap: 20px;
  }
  
  .navbar nav ul li a {
    text-decoration: none;
    color: white;
    font-size: 16px;
    padding: 5px 10px;
    transition: color 0.3s ease;
  }
  
  .navbar nav ul li a:hover {
    color: #ccc; /* Subtle hover effect */
  }
  
  .hero {
    position: relative;
    width: 100%;
    height: 100vh;
    background-image: url('/Users/cliffordroberts/Desktop/PhotoWebsite/saints-touch-photography/src/images/hero.jpg'); /* Set your hero image path here */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    align-items: center; /* Vertically centers the text */
    padding-left: 20px; /* Add padding if needed */
  }
  
  
  .hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .hero-text {
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;
    background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    padding: 10px 20px;
    border-radius: 5px;
    position: relative;
    z-index: 1;

    
  }
  
  .hero-text h1 {
    font-size: 36px;
    margin: 0;
  }
  
  .hero-text p {
    font-size: 16px;
    margin-top: 5px;
  }
  

  .hero::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Dark overlay for better text contrast */
  }
  .about {
    padding: 40px 20px;
    background-color: white;
    text-align: left;
  }
  
  .about h2 {
    font-size: 28px;
    color: #333;
  }
  
  .about-content p {
    font-size: 16px;
    line-height: 1.5;
    color: #555;
  }
  
  .details {
    margin-top: 20px;
  }
  
  .details div {
    margin-bottom: 15px;
  }
  
  .details h3 {
    margin: 0;
    font-size: 18px;
    color: #333;
  }
  
  .details p {
    margin: 5px 0 0;
    font-size: 14px;
    color: #666;
  }

  
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .navbar {
      flex-direction: column;
      align-items: flex-start;
      padding: 10px;
    }
  
    .navbar .logo {
      margin-bottom: 10px;
    }
  
    .navbar nav ul {
      flex-direction: column;
      gap: 10px;
    }
  
    .hero-text {
      bottom: 10px;
      left: 10px;
      padding: 5px 10px;
    }
  
    .hero-text h1 {
      font-size: 28px;
    }
  
    .hero-text p {
      font-size: 14px;
    }
  
    .about {
      padding: 20px;
    }
  
    .about h2 {
      font-size: 24px;
    }
  
    .about-content p {
      font-size: 14px;
    }
  
    .details h3 {
      font-size: 16px;
    }
  
    .details p {
      font-size: 12px;
    }
  }

