.portfolio-showcase {
  padding: 60px 20px;
  background-color: #ffffff;
  text-align: center;
}

.portfolio-showcase h2 {
  font-size: 32px;
  margin-bottom: 40px;
  color: #333;
}

.portfolio-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.portfolio-item {
  position: relative;
  width: 300px;
  height: 450px;
  overflow: hidden;
  border-radius: 10px;
  transition: transform 0.3s ease-in-out;
}

.portfolio-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.portfolio-item:hover {
  transform: scale(1.05);
}

.portfolio-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.portfolio-item:hover .portfolio-overlay {
  opacity: 1;
}

.portfolio-text {
  color: white;
  text-align: center;
  padding: 20px;
}

.portfolio-text h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.portfolio-text p {
  font-size: 16px;
}

